import React, { useState, useRef } from 'react';
import { toast } from 'react-toastify';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { getUserDataAction } from '../redux/actions/user_actions/user_data';
import { LOGIN_URL } from '../apis/url_constants';
import axios from 'axios';

const LoginModal = ({ closeModal, toggleModal }) => {
  const dispatch = useDispatch();
  const [form_inputs, setFormInputs] = useState({});
  const [loading, setLoading] = useState(false);
  const close_btn = useRef();
  const history = useNavigate();

  const toggleReset = () => {
    toggleModal('reset');
    close_btn.current.click();
  };

  const handleLogin = async (email, password) => {
    const data = JSON.stringify({
      email: email,
      password: password,
    });

    const config = {
      method: 'POST',
      url: LOGIN_URL,
      headers: {
        'Content-Type': 'application/json',
      },
      data: data,
    };

    return axios(config).then().catch();
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading(true);
    handleLogin(form_inputs.email, form_inputs.password)
      .then((res) => {
        localStorage.setItem('refresh', res.data.refresh);
        localStorage.setItem('access', res.data.access);
        toast.success('Welcome back!');
        close_btn.current.click();
        dispatch(
          getUserDataAction(res.data.access, () => {
            history('/dashboard', { replace: true });
          })
        );
      })
      .catch((err) => {
        toast.error(err?.response?.data?.detail);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormInputs({ ...form_inputs, [name]: value });
  };

  return (
    <div
      className="modal fade modal-container login-form"
      id="loginModal"
      tabIndex="-1"
      role="dialog"
      aria-labelledby="loginModalTitle"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header align-items-center mh-bg">
            <h5 className="modal-title" id="loginModalTitle">
              Hey, Welcome back!
            </h5>
            <button
              ref={close_btn}
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={() => {
                closeModal(false);
              }}
            >
              <span aria-hidden="true" className="la la-times-circle"></span>
            </button>
          </div>
          <div className="modal-body">
            <form method="post" className="form-box" onSubmit={handleSubmit}>
              <div className="input-box">
                <label className="label-text">Email</label>
                <div className="form-group">
                  <span className="la la-user form-icon"></span>
                  <input
                    required
                    className="form-control form-control-styled"
                    type="email"
                    name="email"
                    onChange={handleChange}
                    value={form_inputs.email}
                    disabled={loading}
                    placeholder="Email address"
                  />
                </div>
              </div>
              <div className="input-box">
                <label className="label-text">Password</label>
                <div className="form-group">
                  <span className="la la-lock form-icon"></span>
                  <input
                    className="form-control form-control-styled"
                    type="password"
                    name="password"
                    onChange={handleChange}
                    value={form_inputs.password}
                    disabled={loading}
                    placeholder="Enter password"
                  />
                </div>
              </div>
              <div className="input-box d-flex align-items-center justify-content-between pb-4 user-action-meta">
                {/* <div className="custom-checkbox">
                  <input type="checkbox" id="keepMeSignedChb" />
                  <label htmlFor="keepMeSignedChb" className="font-size-14">
                    Keep me signed in
                  </label>
                </div> */}
                <Link
                  onClick={toggleReset}
                  className="margin-bottom-10px lost-pass-btn font-size-14"
                >
                  Lost Password?
                </Link>
              </div>
              <div className="btn-box">
                <button type="submit" className="theme-btn gradient-btn w-100">
                  <i className="la la-sign-in mr-1"></i> Login to Account
                </button>
                <br />
                <br />
                <p className="sub-text-box text-right pt-1 font-weight-medium font-size-14">
                  New to UniWork?{' '}
                  <Link
                    className="text-color-2 signup-btn"
                    onClick={() => {
                      toggleModal('register');
                      close_btn.current.click();
                    }}
                  >
                    Create account
                  </Link>
                </p>
              </div>
              {/* <div className="icon-element font-size-16 font-weight-semi-bold mt-5 mb-4 mx-auto">
                OR
              </div>
              <div className="text-center">
                <p className="font-size-15 font-weight-medium">
                  Login with your social network
                </p>
                <ul className="social-profile social-profile-colored py-3">
                  <li>
                    <a
                      href="#"
                      className="google-bg mx-1"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Google"
                    >
                      <i className="lab la-google"></i>
                    </a>
                  </li>
                  <li>
                    <a
                      href="#"
                      className="facebook-bg mx-1"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Facebook"
                    >
                      <i className="lab la-facebook-f"></i>
                    </a>
                  </li>
                  <li>
                    <a
                      href="#"
                      className="twitter-bg mx-1"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Twitter"
                    >
                      <i className="lab la-twitter"></i>
                    </a>
                  </li>
                  <li>
                    <a
                      href="#"
                      className="instagram-bg mx-1"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Instagram"
                    >
                      <i className="lab la-instagram"></i>
                    </a>
                  </li>
                </ul>
              </div> */}
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginModal;
