import React, { useState, useRef } from "react";
import axios from "axios";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import { FORGOT_URL } from "../apis/url_constants";

const ResetPasswordModal = ({ closeModal, toggleModal }) => {
  const [form_inputs, setFormInputs] = useState({});
  const [loading, setLoading] = useState(false);
  const close_btn = useRef();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormInputs({ ...form_inputs, [name]: value });
  };
  const handleSubmit = (e) => {
    setLoading(true);
    e.preventDefault();
    const data = JSON.stringify({
      email: form_inputs.email,
    });

    const config = {
      method: "POST",
      url: FORGOT_URL,
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        if (response.status === 200) {
          toast.success("Password reset instructions have been mailed to you!");
          close_btn.current.click();
        }
      })
      .catch(function (error) {
        console.log(error);
        toast.error("Something went wrong! Please try again!");
      });
    setLoading(false);
  };
  return (
    <div
      className="modal fade modal-container recover-form"
      id="recoverModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="recoverModalTitle"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header align-items-center mh-bg">
            <h5 className="modal-title" id="recoverModalTitle">
              Reset password
            </h5>
            <button
              ref={close_btn}
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={() => {
                closeModal(false);
              }}
            >
              <span aria-hidden="true" className="la la-times-circle"></span>
            </button>
          </div>
          <div className="modal-body">
            <p className="font-size-15 font-weight-medium pb-3">
              Enter your email to reset your password. You will receive an email
              with instructions on how to reset your password. If you are
              experiencing problems resetting your password{" "}
              <Link to="/" className="text-color-2">
                contact us
              </Link>{" "}
              or{" "}
              <a href="mailto:uniwork@gmail.com" className="text-color-2">
                send us an email
              </a>
            </p>
            <form method="post" className="form-box" onSubmit={handleSubmit}>
              <div className="input-box">
                <label className="label-text">Username or email</label>
                <div className="form-group">
                  <span className="la la-user form-icon"></span>
                  <input
                    className="form-control form-control-styled"
                    type="text"
                    name="email"
                    onChange={handleChange}
                    placeholder="Your registered email address"
                  />
                </div>
              </div>
              <div className="btn-box">
                <button
                  disabled={loading}
                  style={
                    loading ? { cursor: "not-allowed" } : { cursor: "pointer" }
                  }
                  type="submit"
                  className="theme-btn gradient-btn w-100"
                >
                  Get New Password <i className="la la-arrow-right ml-1"></i>
                </button>
                <p className="sub-text-box text-right pt-1 font-weight-medium font-size-14">
                  Not a member?{" "}
                  <Link
                    className="text-color-2 signup-btn"
                    onClick={() => {
                      toggleModal("register");
                      close_btn.current.click();
                    }}
                  >
                    Create account
                  </Link>
                </p>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ResetPasswordModal;
