import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { FORGOT_URL } from "../../apis/url_constants";
import "./style.css";
const ResetContainer = ({ route }) => {
  const [pass_one, setPassOne] = useState();
  const [pass_two, setPassTwo] = useState();
  const [bad_pass, setBadPass] = useState(false);
  const history = useNavigate();
  const verify = () => {
    if (pass_one !== pass_two) {
      setBadPass(true);
    } else {
      setBadPass(false);
    }
  };
  const handleSubmit = () => {
    const data = JSON.stringify({
      password: pass_one,
      route: route.split("_")[0],
    });

    const config = {
      method: "PUT",
      url: FORGOT_URL,
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));
        if (response.status === 200) {
          toast.success("Your password has been reset successfully!");
          history("/", { replace: true });
        }
      })
      .catch(function (error) {
        console.log(error.response);
        toast.error(error?.response?.data?.message);
      });
  };

  useEffect(() => {
    verify();
  }, [pass_one, pass_two]);
  return (
    <div className="container text-center reset-container my-5 py-5 col-sm-10 col-md-6 col-lg-6">
      <h1 className="display-4">Reset Password</h1>
      <br />
      <div className="container col-sm-12 col-md-8 col-lg-8">
        <label htmlFor="password1">Enter a new password</label>
        <input
          style={
            bad_pass
              ? { border: "1px solid red" }
              : { border: "1px solid lightgray" }
          }
          onInput={(e) => {
            setPassOne(e.target.value);
          }}
          className="form-control mb-2"
          name="password1"
          type="password"
        />
        <label htmlFor="password2">Repeat your new password</label>
        <input
          style={
            bad_pass
              ? { border: "1px solid red" }
              : { border: "1px solid lightgray" }
          }
          onInput={(e) => {
            setPassTwo(e.target.value);
          }}
          className="form-control mb-5"
          name="password2"
          type="text"
        />
      </div>

      <div className="container-fluid">
        <button
          disabled={bad_pass}
          style={bad_pass ? { cursor: "not-allowed" } : { cursor: "pointer" }}
          onClick={handleSubmit}
          className="theme-btn gradient-btn"
        >
          UPDATE PASSWORD
        </button>
      </div>
    </div>
  );
};

export default ResetContainer;
