import axios from "axios";
import { USER_DETAILS_URL } from "../url_constants";

export const getUserData = (token) => {
  console.log("data", token);
  const config = {
    url: `${USER_DETAILS_URL}`,
    method: "GET",
    headers: {
      Authorization: "Bearer " + token,
    },
  };
  return axios(config).then().catch();
};
