import React, { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import { FORGOT_URL } from "../../apis/url_constants";
import { toast } from "react-toastify";
import Header from "../../components/header";
import Footer from "../../components/footer";
import Loader from "../../components/loader";
import ResetContainer from "./reset_password";
import Hero from "../../components/hero";
import AltHeader from "../../components/alt-header";
const ResetPassword = () => {
  const { route } = useParams();
  const [message, setMessage] = useState("");
  const [loading, isLoading] = useState(false);
  useEffect(() => {
    isLoading(true);
    const config = {
      method: "GET",
      url: FORGOT_URL + route,
    };

    axios(config)
      .then(function (response) {
        if (response.status === 200) {
          console.log(JSON.stringify(response.data));
          console.log("ROUTE VERIFIED");
        }
        isLoading(false);
      })
      .catch(function (error) {
        console.log(error);
        setMessage("This seems to be an expired / invalid route!");
        toast.error("This seems to be an expired / invalid route!");
        isLoading(false);
      });
  }, []);
  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        <>
          <AltHeader />
          {message ? <></> : <ResetContainer route={route} />}
          <Footer />
        </>
      )}
    </>
  );
};

export default ResetPassword;
