import React from "react";
import Home from "./pages/Home";
import Listing from "./pages/Listing";
import Dashboard from "./pages/Dashboard";
import { Route, BrowserRouter as Router, Routes } from "react-router-dom";
import "./styles/style.css";
import "./styles/bootstrap.min.css";
import "./styles/line-awesome.min.css";
import "./styles/owl.carousel.min.css";
import "./styles/owl.theme.default.min.css";
import "./styles/animated-headline.css";
import "./styles/jquery-ui.css";
import "./styles/jquery.fancybox.css";
import "./styles/chosen.min.css";
import Bookings from "./pages/Bookings";
import Bookmarks from "./pages/Bookmarks";
import Reviews from "./pages/Reviews";
import MyProfile from "./pages/MyProfile";
import ListingDetails from "./pages/Listing/ListingDetails";
import ProtectedRoute from "./components/ProtectedRoute";
import ResetPassword from "./pages/ResetPassword";
function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home />}></Route>
        <Route path="/home" element={<Home />}></Route>
        <Route
          path="/dashboard"
          element={<ProtectedRoute component={<Dashboard />} />}
        ></Route>
        <Route
          path="/bookmarks"
          element={<ProtectedRoute component={<Bookmarks />} />}
        ></Route>
        <Route
          path="/reviews"
          element={<ProtectedRoute component={<Reviews />} />}
        ></Route>
        <Route
          path="/myProfile"
          element={<ProtectedRoute component={<MyProfile />} />}
        ></Route>
        <Route
          path="/bookings"
          element={<ProtectedRoute component={<Bookings />} />}
        ></Route>
        <Route
          path="/listing/"
          element={<ProtectedRoute component={<Listing />} />}
        ></Route>
        <Route
          path="/listing/add"
          element={<ProtectedRoute component={<Listing />} />}
        ></Route>
        <Route
          path="/listing/:id"
          element={<ProtectedRoute component={<Listing />} />}
        ></Route>
        <Route
          path="/listing-details/:id"
          element={<ProtectedRoute component={<ListingDetails />} />}
        ></Route>
        <Route path="/reset/:route" element={<ResetPassword />}></Route>
      </Routes>
    </Router>
  );
}

export default App;
