import axios from "axios";
import { GET_CITIES } from "../url_constants";

export const getCities = () => {
  const config = {
    method: "GET",
    url: GET_CITIES,
    headers: {
      "Content-Type": "application/json",
      // 'Authorization': `Bearer ${token}`
    },
  };
  return axios(config).then().catch();
};
